a{
    text-decoration: none;
    color: #333;
}
.act{
    background: #8919e6 !important;
    color: #fff;
}
/* Header */
#kully_logo{
    width: 1200px;
    margin: 0 auto;
    padding: 40px 0;
    border-bottom: 2px solid #ccc;
    text-align: center;
}
.md_com{
    padding-top: 50px;
    padding-bottom: 10px;
    margin: 0 auto;
    text-align: center;
}
.md_com > p{
    font-size: 24px;
    font-weight: bold;
    color: #636363;
}
.kully_category{
    width: 1200px;
    margin: 0 auto;
    margin-bottom: 20px;
}
.kully_category > ul{
    display: flex;
    justify-content:center;
    gap: 20px;
    list-style: none;
    margin: 20px 0;
}
.kully_category > ul > a > li{
    background: #f1f1f1;
    padding: 10px 15px;
    border-radius: 20px;
}

/* Main */
.main_wrap{
    width: 1200px;
    display: flex;
    margin: 50px auto;
    justify-content: center;
    gap: 20px;
}
.kully_items{
    overflow: hidden;
    position: relative;
}
.kully_items a:first-child{
    overflow: hidden;
    display: block;
    height: 390px;
}
.kully_items a img{
    width: 300px;
    transition: .3s;
}
.kully_items a img:hover{
    transform: scale(1.1);
}
.kully_items p{
    font-size: 15px;
}
.kully_cost{
    display: block;
    margin-top: -10px;
    margin-bottom: 5px;
    color: #b8b8b8;
    text-decoration: line-through;
}
.kully_items .discount{
    display: inline-block;
    margin-right: 10px;
    color: orangered;
    font-weight: bold;
}
.kully_items .discount_cost{
    color: #333;
    font-weight: bold;
}

/* Footer */
footer{
    background: #eeeeee;
}
footer > address{
    font-size: normal;
    text-align: center;
    line-height: 50px;
    font-style: 12px;
}

/* swiper */
.swiper-pagination-bullet {
    background-color: #a555e7 !important;
    transition: .3s !important;
}

.swiper-pagination-bullet-active {
    background-color: #8919e6 !important; 
    width: 50px !important;
    border-radius: 5px !important;
}

.swiper-button-prev,
.swiper-button-next {
    color: #8919e6 !important;
}

/* 장바구니 아이콘 */
.cart{
    position: absolute;
    top: 330px;
    left: 220px;
    background: #fff;
    border-radius: 100%;
    padding: 10px 10px;
}
.cart:hover{
    background: #8919e6;
    color: #fff;
}

/* 랜덤배너 */
#banner{
    width: 100%;
    margin: 0 auto;
    text-align: center;
}
#banner img{
    width: 100%;
}

/* 로그인 폼 */
form{
    width: 1200px;
    margin: 100px auto;
    text-align: center;
}
fieldset{
    border: none;
}
legend{
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 10px;
}
.input input{
    width: 350px;
    height: 50px;
    text-indent: 10px;
    margin-top: 10px;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
}
.find_account{
    margin: 0 auto;
    width: 350px;
    display: flex;
    justify-content: right;
    margin-bottom: 10px;
}
.find_account p{
    font-size: 0.813rem;
}
.account_btn input{
    width: 350px;
    height: 50px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
}
.login_btn input{
    background:#55118d;
    color: #fff;
}
.login_btn input:hover{
    background-color: #8919e6;
}
.join_btn input{
    background: #fff;
    border: 1px solid #55118d;
    color: #55118d;
}
.account_result{
    width: 350px;
    background: #55118d;
    color: #fff;
    margin: 0 auto;
}

