@charset "utf-8";
.product{
    width: 1200px;
    margin: 50px auto;
}
.product_title{
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 50px;
}
.product_list{
    display: flex;
    justify-content:center;
    gap: 20px;
}
.product_list > li{
    overflow: hidden;
    display: block;
    width: 25%;
}
.product_list > li img{
    width: 100%;
    height: 370px;
    cursor: pointer;
/*     transition: .3s; */
}
.product_list > li img:hover{
/*     transform: scale(1.1); */
}
.shopping_cart{
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    margin: 5px 0;
    padding: 7px 0;
}
.product_title_text {
    height: 3em;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.5em;
}
.price_discount{
    text-decoration: line-through;
    color: #a7a7a7;
}
.per_price{
    margin-right: 10px;
    color: orangered;
    font-weight: bold;
    display: inline-block;
}
.product_price{
    font-weight: bold;
    display: inline-block;
}
