@charset "uft-8";
*{margin: 0; padding: 0;}
ul, li, ol{list-style: none;}
a{text-decoration: none; color: #333;}

/* 탑배너 */
.top_banner{
    width: 100%;
    height: 50px;
    background: #55118d;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    position: relative;
}
.top_banner > p > span{
    font-weight: bold;
}
.top_banner .x_mark{
    position: absolute;
    right: 100px;
    font-size: 1.5rem;
}
/* 고객메뉴 */
.cus_nav{
    display: flex;
    justify-content: right;
    font-size: 14px;
    margin-right: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.cus_nav span{
    margin: 0 5px;
    color: #ccc;
}
.cus_nav li:first-child{
    color: #55118d;
}

/* 로고 라인 nav */
.logo_line{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.logo_nav{
    display: flex;
    align-items: center;
    gap: 20px;
}
.logo_nav span{
    margin: 0 -15px;
}
.logo_nav :nth-child(2){
    font-weight: bold;
    color: #55118d;
}
.logo_nav :nth-child(2), .logo_nav :nth-child(4){
    font-size: 20px;
}
.logo_nav span, .logo_nav p:last-child{
    color: #ccc;
}
.logo_nav h1 img{
    width: 100px;
}

/* 검색창 */
.search{
    position: relative;
}
.search input{
    padding: 15px 120px;
    border: 1px solid #55118d;
    border-radius: 5px;
}
.search span{
    color: #55118d;
    position: absolute;
    top: 10px;
    right: 20px;
}

/* 아이콘 */
.icons span{
    font-size: 25px;
    margin-right: 20px;
}

/* 카테고리 */
.category{
    display: flex;
    justify-content: space-evenly;
    margin: 10px 0;
}
.cate_nav{
    display: flex;
    justify-content: space-evenly;
    gap: 50px;
    width: 30%;
}